<template>
  <div class="login">
    <CommonHeader title="登录" />
    <div class="logoWrapper">
      <img class="logo" src="../assets/1.gif" />
    </div>
    <van-cell-group class="form">
      <van-field
        v-model="userId"
        required
        clearable
        size="large"
        label="账号"
        placeholder="请输入账号"
      />

      <van-field
        v-model="password"
        type="password"
        label="密码"
        size="large"
        placeholder="请输入密码"
        required
      />
    </van-cell-group>

    <div class="link">
      <RouterLink to="/register">快速注册</RouterLink>
    </div>
    <div class="submit">
      <van-button
        type="primary"
        @click="login"
        class="btn"
        :block="true"
        size="normal"
        >登录</van-button
      >
    </div>
  </div>
</template>
<script>
import axios from "@/axios";
import CommonHeader from "@/components/common/CommonHeader.vue";
export default {
  name: "Login",
  data() {
    return {
      userId: "",
      password: ""
    };
  },
  components: {
    CommonHeader
  },
  methods: {
    login() {
      if (/\w{6,18}$/.test(this.userId)) {
        if (/\w{6,18}$/.test(this.password)) {
          axios({
            method: "post",
            url: "/login",
            data: {
              userId: this.userId,
              password: this.password
            }
          }).then(res => {
            if (res.data.code === 200) {
              sessionStorage.setItem("auth_token", res.data.token);
              this.$toast("登录成功");
              this.$router.push({ path: "/" });
            }
          });
        } else {
          this.$toast("密码只能由6-18位的字母、数字或下划线组成");
        }
      } else {
        this.$toast("账号只能由6-18位的字母、数字或下划线组成");
      }
    }
  }
};
</script>
<style scoped lang="stylus">
.logo
    width 100px
    height @width
.logoWrapper
  text-align center

.link
  padding 5px
  a
    color #24afdd
    text-decoration underline
</style>
